<script>
import firebase from "firebase";
let db = firebase.firestore();

export default {
  data() {
    return {
      datanya:[],
      vdata: {
        email: "",
        password: "",
      },
      Oauth: false,
      cordova: false,
      error: null,
    };
  },
  methods: {
        getData(){
      db.collection(`app`).get().then(res=>{
          let data = res.docs.map(doc=>{
            return {
              id:doc.id,
              ...doc.data()
            }
          })
          this.datanya = data
          this.show=true
          console.log(data)
      })
    },
    loginAuth() {
      firebase
        .auth()
        .signInWithEmailAndPassword(
          this.vdata.email + "@gmail.com",
          this.vdata.password
        )
        .then((data) => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
          this.$router.push("/");
        })
        .catch((err) => {
          this.error = err.message;
        });
    },
    googleAuth() {
      this.Oauth = true;
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then((data) => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
            emailnotif: data.user.email,
            changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }
          };
          db.collection("users")
            .doc(user.uid)
            .set(user, { merge: true })
            .then((res) => {
                this.$store.commit("changeUsers", user);
                localStorage.setItem("users", JSON.stringify(user));
                console.log("user", user);
            });
        })
        .catch((err) => {
          alert("Oops. " + err.message);
        });
    },
    facebookAuth() {
      this.Oauth = true;
      const provider = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then((data) => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
          this.$router.push("/");
        })
        .catch((err) => {
          alert("Oops. " + err.message);
        });
    },
    cordovaGoogleAuth() {
      this.Oauth = true;
      let that = this;
      window.plugins.googleplus.login(
        {
          webClientId:
            "668836186627-g9bing1uplu56rap44dmm8tlopvam44f.apps.googleusercontent.com",
          offline: true,
        },
        function (obj) {
          firebase
            .auth()
            .signInWithCredential(
              firebase.auth.GoogleAuthProvider.credential(obj.idToken)
            )
            .then((data) => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
              };
              that.$store.commit("changeUsers", user);
              localStorage.setItem("users", JSON.stringify(user));
              db.collection("users")
                .doc(data.user.uid)
                .set(user, { merge: true });
              that.$router.push("/");
            })
            .catch((err) => {
              alert("Oops. " + err.message);
            });
        },
        function (msg) {
          alert("error: " + msg);
        }
      );
    },
  },
  mounted() {
    let admin=false
    if(admin){
      this.$router.push('/auth/loginadmin')
    }
    this.getData()
    if (this.Oauth) {
    } else {
      this.$store.dispatch("ceklogin", { true: '/', false: true });
      // setTimeout(() => {
      //   if(this.$store.state.users.auth=='admin'){
      //     this.$router.push('/admin')
      //   }else if(this.$store.state.users.auth=='user'){
      //     this.$router.push('/user')
      //   }
      // }, 1000);
    }
    let that = this;
    document.addEventListener("deviceready", function () {
      that.cordova = true;
    });
  },
};
</script>